.personal {
    position: relative;
    z-index: 7;
    background-color: #fff;
    border-bottom: 1px solid brown;
    border-radius: 20px 20px 0 0;
}

.personal h3 {
    color: brown;
    padding-top: 15px;
    font-family: ui-sans-serif;
    text-transform: uppercase;
    font-size: 32px;
    padding-bottom: 12px;
    font-weight: 600;
}

.personal p {
    padding-bottom: 15px;
    font-size: 20px;
    text-transform: capitalize;
    margin-left: 20px;
    font-weight: 600;
    color: #222;
}

.personal .info {
    border-left: 7px solid brown;
    overflow-x: hidden;
}

.personal .info div {
    font-family: 'Times New Roman', Times, serif;
    position: relative;
    display: flex;
    justify-content: space-between;
    margin: 0 ;
    color: brown !important; 
}

.personal .info :first-child {
    margin-top: 15px;
}

.personal .info div > i {
    margin-left: 10px;
    font-size: 22px;
    margin-right: 10px;
    z-index: 1;
}

.personal .info div p {
    color: brown;
}